
.rain-control {
    box-shadow: 0 0 1px #aaa;
    border-radius: 2px;
    position: fixed;
    font-size: 15px;
    width: 240px;
    padding: 10px 13px;
    bottom: 3px;
    right: 3px;
}
.rain-control .tool a {
    margin-right: 5px;
}
.rain-control .actived {
    border-bottom: 1px solid #ddd;
}




